import Vue from 'vue'
class Check {
  constructor () {
    this.testName = /^[\u4e00-\u9fa5]+(\.[\u4e00-\u9fa5]+)*$/
    this.testPhone = /^(13[0-9]|14[579]|15[0-9]|17[0135678]|18[0-9]|19[8-9]|166)\d{8}$/
    this.testBank = /^(\d{15,19})$/
    this.testCode = /^\d{6}$/
    this.testIdCard = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
    this.testSpecialChar = /^[\u4E00-\u9FA5A-Za-z0-9\s.]{1,40}$/
    this.testEmail = /^[a-z\d]+(\.[a-z\d]+)*@([\da-z](-[\da-z])?)+(\.{1,2}[a-z]+)+$/
    this.testMessage = /^[\u4E00-\u9FA5A-Za-z0-9\s.,]{0,100}$/
    this.testPassWord = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/
    Vue.prototype.$check = this
  }

  checkPhone (value) {
    return this.testPhone.test(value)
  }
  checkPassword(value){
    return this.testPassWord.test(value)
  }
  checkName (value) {
    return this.testName.test(value)
  }
  checkBank (value) {
    return this.testBank.test(value)
  }
  checkCode (value) {
    return this.testCode.test(value)
  }
  checkIdCard (value) {
    return this.testIdCard.test(value)
  }
  checkSpecialChar (value) {
    return this.testSpecialChar.test(value)
  }
  checkMessage (value) {
    return this.testMessage.test(value)
  }
  checkEmail (value) {
    return this.testEmail.test(value)
  }
  isWeChat () {
    let ua = navigator.userAgent.toLowerCase()
    return /micromessenger/.test(ua)
  }
  isQQ () {
    let ua = navigator.userAgent.toLowerCase()
    return /qq\/([\d]+)/.test(ua)
  }
  isAndroid () {
    let ua = navigator.userAgent.toLowerCase()
    return /android/.test(ua)
  }
  isIOS () {
    let ua = navigator.userAgent.toLowerCase()
    return /iphone|ipad|ipod|itouch/.test(ua)
  }
  iOSdown(){
    return `https://itunes.apple.com/us/app/%E7%88%B1%E7%94%A8%E5%95%86%E5%9F%8E-%E5%88%86%E6%9C%9F%E8%B4%AD%E7%89%A9%E9%A2%86%E4%BC%98%E6%83%A0%E5%88%B8/id1315925508?l=zh&ls=1&mt=8`
  }
  androidDown(){
    return `http://imtt.dd.qq.com/16891/3098D27ECEE9509D6580A6475AAA47DE.apk?fsname=com.qkkj.wallet_1.0.7.1_10701.apk&csr=1bbd`
  }
}
export default Check



