export default {
namespaced:true,
   state:{
    navBar:true,    //  
    showBack:true,  //  
    showMore:true,  //  
    navTitle:'',    //  
   },
   mutations:{
       setnavBar(state,data){
        state.navBar = data
       },
       setshowBack(state,data){
        state.showBack = data
       },
       setshowMore(state,data){
        state.showMore = data
       },
       setnavTitle(state,data){
        state.navTitle = data
       },
   }
}


