import Vue from 'vue'
import Router from 'vue-router'
import home from '../views/home'
import cart from '../views/cart'
import personal from '../views/personal'
import login from '../views/login'
import register from '../views/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      meta: {
        title: ''
      }
    },
    {
      path: '/cart',
      name: 'cart',
      component: cart,
      meta: {
        title: ''
      }
    },
    {
      path: '/personal',
      name: 'personal',
      component: personal,
      meta: {
        title: ''
      }
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: ''
      }
    },
    {
      path: '/register',
      name: 'register',
      component: register,
      meta: {
        title: ''
      }
    },
  ]
})



